<template>
  <div class="table-box">
    <a-table
      class="table"
      :pagination="{showQuickJumper:true,total,current,pageSize}"
      @change="onchange"
      :rowKey="(v,i)=>i"
      :columns="table"
      :dataSource="tableData"
    >
      <template slot="operation" slot-scope="text,v,i">
        <l-link @click="$emit('addTab',`${v.processCode}-`,v.processCode,v)"></l-link>
        <l-link @click="operation(v)"></l-link>
      </template>
    </a-table>
  </div>
</template>
<script>
import { getProcess, saveProcess, deleteProcess } from "@/api/craft";
export default {
  props: {
    code: {
      default: ""
    },
    name: {
      default: ""
    }
  },
  data() {
    return {
      table: [
        {
          title: "",
          dataIndex: "processCode",
          scopedSlots: { customRender: "processCode" }
        },
        {
          title: "",
          dataIndex: "processName",
          scopedSlots: { customRender: "processName" }
        },
        {
          title: "",
          dataIndex: "processRemark",
          scopedSlots: { customRender: "processRemark" }
        },
        {
          title: "",
          dataIndex: "processHours",
          scopedSlots: { customRender: "processHours" }
        },
        {
          title: "",
          dataIndex: "processAuxiliaryHours",
          scopedSlots: { customRender: "processAuxiliaryHours" }
        },
        {
          title: "",
          dataIndex: "processMachiningCost",
          scopedSlots: { customRender: "processMachiningCost" }
        },
        {
          title: "",
          dataIndex: "processEquipment",
          scopedSlots: { customRender: "processEquipment" }
        },
        {
          title: "",
          dataIndex: "processFixture",
          scopedSlots: { customRender: "processFixture" }
        },
        {
          title: "",
          dataIndex: "processCheck",
          scopedSlots: { customRender: "processCheck" }
        },
        {
          title: "",
          dataIndex: "processTool",
          scopedSlots: { customRender: "processTool" }
        },
        {
          title: "",
          dataIndex: "processWorkCenter",
          scopedSlots: { customRender: "processWorkCenter" }
        },
        {
          title: "",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" }
        }
      ],
      tableData: [],
      total: 0,
      pageSize: 10,
      current: 1,
      edit: null
    };
  },
  mounted() {
    this.getTable();
  },
  methods: {
    async getTable(current = this.current) {
      let data = Array(20)
        .fill({})
        .map((v, i) => {
          return {
            processCode: "" + i,
            processName: "",
            processRemark: "",
            processHours: "",
            processAuxiliaryHours: "",
            processMachiningCost: "",
            processEquipment: "",
            processFixture: "",
            processCheck: "",
            processTool: "",
            processWorkCenter: ""
          };
        });
      this.tableData = data;
      return;
      let { pageSize, code, name } = this;
      let res = await getProcess({
        pageNow: current,
        pageSize,
        processCode: code,
        processName: name
      });
      if (res.data.code != this.$global.successCode) return;
      this.current = current;
      this.edit = null;
      this.total = res.data.data.total;
      this.tableData = res.data.data.list;
    },
    onchange(v) {
      let { current, pageSize } = v;
      Object.assign(this, { current, pageSize });
      this.getTable();
    },
    async operation(v) {
      let res = await deleteCraft(v.technologyId);
      if (res.data.code != this.$global.successCode) return;
      this.getTable();
    }
  }
};
</script>
<style lang="less" scoped>
.table-box {
  height: 100%;
  width: 100%;
  padding: 20px;
  overflow-x: auto;
  .table {
    input {
      width: 120px;
    }
  }
}
</style>